body {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto;
  max-width: 100%;
  padding: 8px;
  overflow-x: hidden;
}
footer {
  position: fixed;
  margin-top: 32px;
  left: 10px;
  bottom: 10px;
}
a,
a:visited,
a:hover,
a:focus,
a:active {
  color: #262626;
}
.links {
  position: absolute;
  top: -120px;
  right: 16px;
}
.links a,
.links a:visited,
.links a:hover,
.links a:focus,
.links a:active {
  color: white;
  text-decoration: none!important;
}
.container {
  display: grid;
  grid-template-columns: 50% auto;
  height: auto;
  align-self: center;
  margin: 0 50px;
}
.title {
  z-index: -1;
  height: 120px;
  border-radius: 8px;
  background-image: url('/images/header.jpg');
  background-size: cover;
}
.logo-image {
  margin: 32px;
}
.navbar {
  position: absolute;
  color: white;
  height: 80px;
  width: 100%;
  padding-top: 16px;
  padding-right: 48px;
  text-align: right;
  -webkit-animation: 1s ease-out 0s 1 slideInFromTop;
  animation: 1s ease-out 0s 1 slideInFromTop;
}
.links:hover > span > a {
  color: grey;
  -webkit-transition: color .5s;
  -o-transition: color .5s;
  transition: color .5s;
}
.links:hover > span:hover > a {
  color: white;
  -webkit-transition: color .5s;
  -o-transition: color .5s;
  transition: color .5s;
}
.links > span {
  padding: 16px;
  -webkit-transition: color .5s;
  -o-transition: color .5s;
  transition: color .5s;
}
.intro {
  padding-top: 36px;
}
.intro > h1 {
  color: white;
  margin-left: 160px;
}
.intro > h3 {
  color: white;
  margin-left: 164px;
}
.intro > h1:nth-child(1) {
  -webkit-animation: 0.5s ease-out 0s 1 slideInFromLeft;
  animation: 0.5s ease-out 0s 1 slideInFromLeft;
}
.intro > h1:nth-child(2) {
  -webkit-animation: 1s ease-out 0s 1 slideInFromLeft;
  animation: 1s ease-out 0s 1 slideInFromLeft;
}
.photo {
  border-radius: 50%;
  margin-left: 480px;
  margin-top: 104px;
  position: absolute;
  right: 32px;
  -webkit-animation: 1s ease-out 0s 1 slideInFromRight;
  animation: 1s ease-out 0s 1 slideInFromRight;
  -webkit-transition: 0.1s height ease-out;
  -o-transition: 0.1s height ease-out;
  transition: 0.1s height ease-out;
}
.photo:hover {
  height: 100px;
  -webkit-transition: 0.1s height ease-out;
  -o-transition: 0.1s height ease-out;
  transition: 0.1s height ease-out;
}
@-webkit-keyframes slideInFromLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-2%);
    transform: translateX(-2%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}
@keyframes slideInFromLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-2%);
    transform: translateX(-2%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
}
@-webkit-keyframes slideInFromRight {
  0% {
    right: 0;
  }
  100% {
    right: 32px;
  }
}
@keyframes slideInFromRight {
  0% {
    right: 0;
  }
  100% {
    right: 32px;
  }
}
@-webkit-keyframes slideInFromRightMargin {
  0% {
    margin-left: 32px;
  }
  100% {
    margin-left: 0;
  }
}
@keyframes slideInFromRightMargin {
  0% {
    margin-left: 32px;
  }
  100% {
    margin-left: 0;
  }
}
@-webkit-keyframes slideInFromTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20%);
    transform: translateY(-20%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@keyframes slideInFromTop {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20%);
    transform: translateY(-20%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@-webkit-keyframes slideInFromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20%);
    transform: translateY(20%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@keyframes slideInFromBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(20%);
    transform: translateY(20%);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
@-webkit-keyframes stackCardsEntry {
  0% {
    opacity: 0;
    -webkit-transform: rotateY(-35deg) rotateX(15deg) translateY(20%) translateX(-20%);
    transform: rotateY(-35deg) rotateX(15deg) translateY(20%) translateX(-20%);
  }
  100% {
    opacity: 1;
    -webkit-transform: rotateY(-35deg) rotateX(15deg) translateY(0) translateX(0);
    transform: rotateY(-35deg) rotateX(15deg) translateY(0) translateX(0);
  }
}
@keyframes stackCardsEntry {
  0% {
    opacity: 0;
    -webkit-transform: rotateY(-35deg) rotateX(15deg) translateY(20%) translateX(-20%);
    transform: rotateY(-35deg) rotateX(15deg) translateY(20%) translateX(-20%);
  }
  100% {
    opacity: 1;
    -webkit-transform: rotateY(-35deg) rotateX(15deg) translateY(0) translateX(0);
    transform: rotateY(-35deg) rotateX(15deg) translateY(0) translateX(0);
  }
}
@-webkit-keyframes opacityAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes opacityAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.home {
  text-align: center;
  margin: 16px;
  margin-top: 32px;
}
.home > h1 {
  font-size: 40px;
  font-weight: 800;
}
.home > p {
  font-size: 16px;
}
.stack {
  margin-top: 32px;
}
.skills {
  -webkit-perspective: 1000px;
  perspective: 1000px;
}
.skills img {
  width: 80px;
}
.skills > div {
  display: grid;
  grid-template-columns: 40% auto;
  width: 32vw;
  -webkit-transform: rotateY(-35deg) rotateX(15deg);
  transform: rotateY(-35deg) rotateX(15deg);
  position: absolute;
  border-bottom: 4px solid rgba(0, 0, 0, 0.2);
  padding: 32px;
  border-radius: 10px;
  cursor: pointer;
  mix-blend-mode: darken;
  -webkit-transition: -webkit-transform 0.1s ease-out;
  transition: -webkit-transform 0.1s ease-out;
  -o-transition: transform 0.1s ease-out;
  transition: transform 0.1s ease-out;
  transition: transform 0.1s ease-out, -webkit-transform 0.1s ease-out;
  text-align: start;
  -webkit-animation: 1s ease-out 0s 1 stackCardsEntry;
  animation: 1s ease-out 0s 1 stackCardsEntry;
}
.skills > div > span > strong {
  font-size: 32px;
  font-weight: 800;
}
.skills > div:nth-child(1) {
  height: 200px;
  color: #cdcdcd;
  top: 0px;
  z-index: 2;
  background: #364156;
}
.skills > div:nth-child(2) {
  color: white;
  top: 180px;
  z-index: 1;
  background: #52c1df;
}
.skills > div:nth-child(3) {
  color: #04576d;
  top: 360px;
  background: #DBE4EE;
}
.skills > div:hover {
  -webkit-transform: rotateY(-15deg) rotateX(7deg) scale(1.03);
  transform: rotateY(-15deg) rotateX(7deg) scale(1.03);
  -webkit-transition: -webkit-transform 0.2s ease-out;
  transition: -webkit-transform 0.2s ease-out;
  -o-transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out;
  transition: transform 0.2s ease-out, -webkit-transform 0.2s ease-out;
  z-index: 3;
  mix-blend-mode: normal;
}
.stack-border {
  border: 1px solid black;
  border-left: none;
  height: 680px;
  width: 23px;
}
.stack-arrow {
  border: .5px solid black;
  width: 32px;
  margin-left: 24px;
  position: absolute;
  top: 340px;
}
.stack-description {
  display: inline;
  position: absolute;
  top: 20px;
  left: 64px;
  text-align: start;
}
.stack-description > div {
  height: 220px;
  -webkit-animation: 1s ease-out 0s 1 opacityAnimation;
  animation: 1s ease-out 0s 1 opacityAnimation;
  line-height: 2;
}
.stack-description > div > h1 {
  -webkit-animation: 1s ease-out 0s 1 slideInFromRightMargin;
  animation: 1s ease-out 0s 1 slideInFromRightMargin;
}
.stack-short-note {
  margin-top: 64px;
  -webkit-animation: 1s ease-out 0s 1 slideInFromLeft;
  animation: 1s ease-out 0s 1 slideInFromLeft;
}
.anticon-heart {
  color: #e74c3c;
  -webkit-transition: 0.2s font-size ease-out;
  -o-transition: 0.2s font-size ease-out;
  transition: 0.2s font-size ease-out;
}
.anticon-check {
  color: green;
  -webkit-transition: 0.2s font-size ease-out;
  -o-transition: 0.2s font-size ease-out;
  transition: 0.2s font-size ease-out;
}
.anticon-heart:hover {
  font-size: 110%;
  -webkit-transition: 0.2s font-size ease-out;
  -o-transition: 0.2s font-size ease-out;
  transition: 0.2s font-size ease-out;
}
.mobilemenu,
.sidebar {
  display: none;
}
.worksTransition-appear {
  opacity: 0;
  -webkit-transform: translateX(-100px);
  -ms-transform: translateX(-100px);
  transform: translateX(-100px);
}
.worksTransition-appear.worksTransition-appear-active {
  opacity: 1;
  -webkit-transform: translateX(0px);
  -ms-transform: translateX(0px);
  transform: translateX(0px);
  -webkit-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}
.experience,
.contact {
  margin: 32px;
}
.contact div {
  margin-top: 8px;
}
.contact h4 {
  display: inline;
}
.experience .ant-timeline-item-content,
.contact {
  -webkit-animation: 1s ease-out 0s 1 slideInFromTop;
  animation: 1s ease-out 0s 1 slideInFromTop;
}
footer {
  background: white;
}
.combined-wrapper {
  padding: 0;
  background: url('/images/header.jpg');
  height: 148px;
  position: relative;
  width: 100%;
  border-radius: 8px;
}
.combined {
  color: #fff;
  width: 316px;
  height: 100px;
  position: absolute;
  top: 0;
  left: 152px;
  bottom: 0;
  margin: auto;
}
.combined-shape {
  position: relative;
}
.combined-shape > * {
  width: 50%;
  height: 64px;
  position: absolute;
  overflow: hidden;
}
.combined-shape > * > * {
  width: 100%;
  height: 100%;
  background: #fff;
}
.combined-shape .shape-left {
  left: 0;
}
.combined-shape .shape-right {
  right: 0;
}
.combined .animated-title {
  font-size: 40px;
  letter-spacing: 8px;
  position: absolute;
  width: 350px;
  overflow: hidden;
}
.combined-bar {
  width: 100%;
  height: 2px;
  background: #fff;
  position: absolute;
  top: 70px;
}
.combined .content {
  font-size: 12px;
  text-align: center;
  position: absolute;
  top: 78px;
}
@media only screen and (max-width: 1026px) {
  body {
    padding: 0px;
    max-width: 100%;
    overflow-x: hidden;
  }
  .combined {
    left: 80px;
  }
  .combined .animated-title {
    font-size: 32px;
  }
  .combined-wrapper {
    border-radius: 0px;
  }
  .intro,
  .logo-image,
  .photo {
    display: none;
  }
  .home-head {
    font-size: 6vw!important;
  }
  .title > img {
    position: absolute;
    margin: 8px;
    height: 8vh;
  }
  .title {
    height: 80px;
    overflow: hidden;
  }
  .navbar {
    margin-top: 12px;
    padding-right: 8px;
  }
  .links {
    display: none;
  }
  .photo {
    height: 6vh;
    top: 56px;
  }
  .stack-short-note-div,
  .skills > div > span,
  .stack-arrow,
  .title > img {
    display: none!important;
  }
  .skills > div {
    display: grid;
    grid-template-columns: 100% auto;
    -webkit-transform: rotateY(0) rotateX(0);
    transform: rotateY(0) rotateX(0);
    position: relative;
    border-bottom: 4px solid rgba(0, 0, 0, 0.2);
    padding: 1vw;
    border-radius: 1vh;
    -webkit-transition: -webkit-transform 0.1s ease-out;
    transition: -webkit-transform 0.1s ease-out;
    -o-transition: transform 0.1s ease-out;
    transition: transform 0.1s ease-out;
    transition: transform 0.1s ease-out, -webkit-transform 0.1s ease-out;
    text-align: start;
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
    height: initial!important;
    -webkit-animation: none;
    animation: none;
  }
  .stack-description {
    font-weight: 800;
    left: 24px;
  }
  .stack-border {
    margin-left: -24px;
  }
  .stack-description > div {
    font-size: 1.3vh!important;
    width: 60vw;
  }
  footer {
    margin-top: 80px;
  }
  .mobilemenu {
    display: block!important;
    position: absolute;
    margin-top: 24px;
    margin-left: 24px;
    z-index: 5;
  }
  .mobilemenu > button,
  .mobilemenu > button:active,
  .mobilemenu > button:focus,
  .mobilemenu > button:hover {
    background-color: rgba(0, 0, 0, 0);
    border: none;
    -webkit-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
  }
  .sidebar {
    position: absolute;
    display: block;
    background: black;
    height: 500vh;
    padding: 2vw;
    z-index: 3;
    width: 30vw;
  }
  .open {
    margin-left: 0;
    -webkit-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
  }
  .close {
    margin-left: -30vw;
    -webkit-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
  }
  .sidebar > div {
    padding-top: 32px;
    margin-left: 16px;
  }
  .sidebar > div > a {
    color: white;
  }
}



